/* a.css */
.moving-container {
    overflow: hidden;
    width: 100%;
  }
  
  .marquee {
    display: flex;
    animation-duration: 35s; /* Set the animation duration here */
    animation-timing-function: linear;
    animation-iteration-count: infinite;
  }
  
  .animate-left {
    animation-name: marqueeAnimationLeft;
  }
  
  .animate-right {
    animation-name: marqueeAnimationRight;
  }
  
  @keyframes marqueeAnimationLeft {
    0% {
      transform: translateX(100%);
    }
    100% {
      transform: translateX(-100%);
    }
  }
  
  @keyframes marqueeAnimationRight {
    0% {
      transform: translateX(-100%);
    }
    100% {
      transform: translateX(100%);
    }
  }
  
  .image {
    /* Add any styling for the images here (e.g., max-width, max-height) */
  }
  